// ACSHBToolbar START
.acshb-toolbar {
  position: relative;
  transition: $transition-base;

  img {
    height: 75px;

    @include media-breakpoint-only(xs) {
      height: 75px;
    }
  }

  .acshb-toolbar-button {
    cursor: pointer;
  }
}
// ACSHBToolbar END

// ACSHBSidebar START
.acshb-sidebar {
  position: relative;
  min-width: 120px;
  min-height: 100vh;
  animation-duration: 0.5s;
  transition: $transition-base;

  .acshb-sidebar-toogle {
    position: absolute;
    top: 25px;
    right: -38px;
    width: 40px;
    height: 40px;
    padding: 0.5em;
    color: $white;
    background-color: lighten(theme-color("primary"), 15%);
    border-bottom-right-radius: 0.75em;
    border-top-right-radius: 0.75em;
    z-index: -1;

    span {
      display: block;
      font-size: 1.25em;
      margin-top: 2px;
    }
  }

  .acshb-sidebar-item {
    border-radius: 0;
    &:not(.acshb-sidebar-sub-item) {
      min-height: 105px;
    }

    .acshb-sidebar-link {
      color: $body-color;

      &:hover,
      &:focus,
      &:active {
        cursor: pointer;
        color: theme-color("primary");
      }

      * {
        margin: 0;
      }
    }

    .collapse {
      overflow-y: auto;
    }
  }

  .active-link {
    color: theme-color("primary");
    border-left: 4px solid theme-color("primary");

    .acshb-sidebar-link {
      color: theme-color("primary");
    }

    .noactive-sub-link {
      .acshb-sidebar-link {
        border: none;
        color: $body-color;
      }
    }
  }
}

.acshb-sidebar-mobile {
  position: absolute;
  display: none;
  top: 80px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  overflow-y: auto;
  z-index: 1;
  transition: $transition-base;
}

.acshb-sidebar-open {
  display: block;
  margin-left: 0;
}

.acshb-sidebar-close {
  margin-left: -130px;
}
// ACSHBSidebar END

// Page START
.page {
  max-width: 100%;
}
// Page END

// ACSHBButton START
.btn {
  &:hover,
  &:focus,
  &:active,
  &:visited {
    box-shadow: none;
  }
}

.btn-circle {
  width: 35px;
  height: 35px;
  text-align: center;
  padding: 6px 0;
  font-size: $font-size-base;
  line-height: 1.428571429;
  border-radius: 2em;
}

.btn-circle.btn-lg {
  width: 50px;
  height: 50px;
  padding: 10px 16px;
  font-size: $font-size-lg;
  line-height: 1.33;
  border-radius: 25px;
}

.btn-loading {
  .acshb-icon-loader {
    display: inline-block;
    @include rotate;
  }
}
// ACSHBButton END

// ACSHBDropdown START
.acshb-dropdown {
  .dropdown-toggle {
    &::after {
      margin-left: 0.5em;
    }
  }
  .dropdown-menu {
    ul {
      li {
        .dropdown-item-icon {
          min-width: 1em;
          min-height: 1em;
        }

        &:hover,
        &:focus {
          cursor: pointer;
        }
      }
    }
  }
}
// ACSHBDropdown END

