// ACSHBAccordion START
.acshb-accordion-list {
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: transparent;
  border-radius: 0.4rem;
  overflow: hidden;

  &__item {
    cursor: pointer;
    margin-bottom: 0.5em;
  }
}

.acshb-accordion-item {
  $self: &;

  &--opened {
    #{ $self }__icon {
      transform: rotate(180deg);
    }

    #{ $self }__inner {
      max-height: 100rem;
      transition-timing-function: cubic-bezier(0.895, 0.03, 0.685, 0.22);
      transition-duration: 0.5s;
      transition-property: max-height;
    }

    #{ $self }__content {
      opacity: 1;
      transform: translateY(0);
      transition-delay: 0.2s;
      transition-timing-function: ease-in-out;
      transition-duration: 0.2s;
      transition-property: opacity, transform;
    }
  }

  &__line {
    display: block;
    padding: 0.8rem 1.2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    z-index: 2;
    position: relative;
  }

  &__title {
    font-size: 1rem;
    margin: 0;
    font-weight: 500;
    color: $gray-700;
  }

  &__icon {
    width: 1.2rem;
    height: 1.2rem;
    transition: transform 0.3s ease-in-out;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url(data:image/png;base64,);
    opacity: 0.6;
  }

  &__inner {
    max-height: 0;
    overflow: hidden;
    text-transform: cubic-bezier(0.95, 0.05, 0.795, 0.035);
    transition-duration: 0.5s;
    transition-property: max-height;
    z-index: 1;
    position: relative;
    background-color: $white;
  }

  &__content {
    opacity: 0;
    transform: translateY(-1rem);
    transition-timing-function: linear, ease;
    transition-duration: 0.1s;
    transition-property: opacity, transform;
    transition-delay: 0.5s;
    padding: 0 1.2rem 1.2rem;
  }
}
// ACSHBAccordion END

// ACSHBMap START
.acshb-map .active-city {
  fill: theme-color("primary");
}

.acshb-map path {
  cursor: pointer;
  fill: rgb(249, 250, 251);
  stroke: rgba(0, 0, 0, 0.4);
  stroke-width: 0.25px;
}

.acshb-map path:hover {
  fill: theme-color("primary");
}

.map-data {
  position: relative;
  width: 75%;
  min-height: 75px;
  margin: auto;
  padding: 14px;
  text-align: center;
  transition: all 0.25s;
  font-size: 1.25em;
}

#guney-kibris {
  pointer-events: none;
}
// ACSHBMap END

// ACSHBCard START
.acshb-card {
  width: 100%;
  border-radius: 6px;
  box-shadow: 0 2px 2px rgba(204, 197, 185, 0.5);
  background-color: #ffffff;
  color: #252422;
  margin-bottom: 20px;
  position: relative;
  z-index: 1;

  .content {
    padding: 15px 15px 10px 15px;
  }

  .icon-big {
    font-size: 3em;
    min-height: 64px;
  }

  .numbers {
    font-size: 2em;
    text-align: right;
    line-height: 16px;

    p {
      font-size: 16px;
    }
  }

  .footer {
    padding: 0;
    line-height: 30px;

    hr {
      margin-top: 5px;
      margin-bottom: 5px;
    }

    div {
      display: inline-block;
    }
  }

  .stats {
    // color:$gray-500;

    span {
      color: inherit;
      margin-right: 2px;
      min-width: 15px;
      display: inline-block;
    }
  }
}
// ACSHBCard END

// FormField START
.rw-widget {
  .rw-widget-container {
    input {
      font-weight: $font-weight-light;
    }
  }

  .rw-widget-input {
    padding: 0 $input-btn-padding-x;

    .rw-input {
      padding: 0;
    }

    input {
      padding: 0;
    }
  }

  li {
    border: none !important;
  }
}

.rw-state-focus {
  box-shadow: none !important;

  .rw-widget-input,
  .rw-widget-container {
    border: $border-width solid $input-focus-border-color !important;
    box-shadow: none !important;
    &:hover,
    &:focus {
      border: $border-width solid $input-focus-border-color !important;
      box-shadow: none !important;
    }
  }
}

.form-field-error {
  font-size: 0.75em;
}

.form-field-warning {
  font-size: 0.75em;
  color: theme-color("warning");
}
// FormField END

// ACSHBDatatable START
.acshb-datatable {
  position: relative;

  .acshb-datatable-tools {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 1em;

    * {
      margin: 0;
    }
  }
}
// ACSHBDatatable END

