// ACSHBStepper START
.acshb-stepper {
  display: relative;

  .acshb-stepper-header {
    position: relative;

    .acshb-stepper-step {
      display: flex;
      flex-direction: column;
      align-items: center;

      &:first-child {
        align-items: flex-start;
      }

      &:last-child {
        align-items: flex-end;
      }

      div {
        &:first-child {
          width: 45px;
          height: 45px;
          background-color: #fff;
        }

        .acshb-stepper-dot {
          border-radius: 50%;
          max-width: 25%;
          max-height: 25%;
          background-color: $gray-500;
        }

        .acshb-stepper-active-dot {
          border-radius: 50%;
          max-width: 40%;
          max-height: 40%;
          background-color: theme-color("primary");
        }
      }

      .acshb-stepper-title {
        font-weight: 300;
      }
    }
  }

  .acshb-stepper-hr {
    position: absolute;
    top: 25%;
    left: 0px;
    width: 100%;
    height: 2px;
    background-color: #000;
    opacity: 0.1;
    z-index: -1;
  }
}
// ACSHBStepper END

